ks-compare-popup {
    display: flex;
    flex-direction: column;
    align-items: center;

    position: fixed;
    overflow: hidden;

    font-family: var(--font-emphasis);

    bottom: 0px;
    left: 0px;
    right: 0px;

    z-index: 1000000;
    
    background-color: #f2f2f2;
    border-top: solid 1px #c2c2c2;

    transition: transform 0.4s ease;
}

ks-compare-popup .message {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 65px;
    padding: 15px;
    box-sizing: border-box;

    text-align: center;
    font-size: 16px;
    line-height: 17px;
}

ks-compare-popup .message p {
    margin: 0;
    width: 100%;
}

ks-compare-popup .products {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 80px;
    padding: 0px 15px 15px 15px;
    gap: 5px;
    box-sizing: border-box;

    font-size: 14px;
    line-height: 14px;
}

ks-compare-popup .products .product {
    display: flex;
    padding: 5px 10px;
    gap: 5px;
    width: 100%;
    background-color: #ffffff;
    border: 1px solid #e3e3e3;
    height: 60px;
    border-radius: 10px;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
}

ks-compare-popup .products .product.empty {
    background-color: transparent;
    border: solid 1px #a7a7a7;
    opacity: 0.2;
}

ks-compare-popup .products .product ks-img3 {
    max-width: 50px;
    max-height: 50px;
}

ks-compare-popup .products .product a {
    color: #151515;
    text-decoration: none;
}

ks-compare-popup:not([hide]):not([extend]) {
    transform: translateY(80px);
}

ks-compare-popup:not([hide])[extend] {
    transform: translateY(0px);
}

ks-compare-popup[hide] {
    transform: translateY(150px);
}

ks-compare-popup:not([show]) {
    transform: translateY(150px);
    display: none;
}

ks-compare-popup p {
    margin: 0;
    max-width: 100%;
}

ks-compare-popup .buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 5px;
}

ks-compare-popup .buttons > * {
    width: max-content;
}

ks-compare-popup .buttons > *:first-child {
    margin-right: 5px;
}

ks-compare-popup .type {
    margin-bottom: 10px;
}

ks-compare-popup .name {
    font-size: 18px;
    font-weight: 700;
}

ks-compare-popup[blink] .products {
    animation: blink-animation 0.5s ease-in;
}

@keyframes blink-animation {
    0% { opacity: 0.3; }
    100% { opacity: 1; }
}

@media (max-width: 920px) {
    ks-compare-popup .products {
        display: block;
        height: 190px;
    }

    ks-compare-popup .products .product {
        height: 55px;
        margin-bottom: 5px;
    }

    ks-compare-popup:not([hide]):not([extend]) {
        transform: translateY(190px);
    }

    ks-compare-popup[hide],
    ks-compare-popup:not([show]) {
        transform: translateY(260px);
    }

    ks-compare-popup[extend] .buttons p {
        font-size: 14px;
    }
}

@media (max-width: 420px) {
    ks-compare-popup[extend] .buttons p {
        content-visibility: hidden;
    }
}