ks-info-cards {
    display: block;
    width: 100%;
    margin: 20px auto 0px auto;
    max-width: 1260px;
    margin: auto;
}

ks-info-cards .swiper-wrapper {
    outline-offset: 4px;
}

ks-info-cards .swiper-slide {
    text-align: center;
    background: #ffffff;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    font-size: 15px;

    width: 100%;
    max-width: 300px;
    height: auto !important;

    border: #ededed 1px solid;
    border-radius: 6px;
    overflow: hidden;
}

ks-info-cards .swiper-slide ks-img3 {
    height: 200px;
    width: 100%;
}

ks-info-cards .swiper-slide ks-img3 ks-loader {
    display: none;
}

ks-info-cards .swiper-slide div {
    padding: 15px 10px;
}