ks-listing-info {
    display: flex;
    max-width: 1200px;
    background-color: #151515;
    color: #fbfbfb;
    margin: 20px auto 0 auto;
    border-radius: 10px;
    overflow: hidden;
}

ks-listing-info .content {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

ks-listing-info ks-img3 {
    max-width: 250px;
}
ks-listing-info ks-button > * {
    background-color: #232323;
}

ks-listing-info h3 {
    font-size: 16px;
    margin-bottom: 5px;
}

ks-listing-info p {
    font-size: 14px;
    margin: 0 0 15px 0;
}

@media screen and (max-width: 1200px) {
    ks-listing-info {
        border-radius: 0;
        margin: 0;
    }
}

@media screen and (max-width: 640px) {
    ks-listing-info p {
        font-size: 13px;
    }
}