import { Component, h, Prop, Host } from '@stencil/core';

import Swiper from 'swiper';
import {Pagination, A11y, Keyboard} from 'swiper/modules';
Swiper.use([Pagination, Keyboard, A11y]);

@Component({
  tag: 'ks-info-cards',
  styleUrl: 'info-cards.css'
})
export class InfoCards {
  @Prop() data: {image: string, text: string}[];

  swiper: Swiper;

  componentDidLoad() {
    this.swiper = new Swiper("ks-info-cards .swiper", {
        grabCursor: true,
        slidesPerView: "auto",
        spaceBetween: 18,
      });
  }

  render() {
    return <Host>
        <div class="swiper">
            <div class="swiper-wrapper" tabindex={0} onFocusin={()=>this.swiper.keyboard.enable()} onFocusout={()=>this.swiper.keyboard.disable()}>
                {this.data.map(item => 
                    <div class="swiper-slide">
                        <ks-img3 sync fit="cover" image={item.image}/>
                        <div>{item.text}</div>
                    </div>
                )}
            </div>
        </div>
    </Host>;
  }
}
