import { Component, h, Element, Prop, Host, Method, Listen, Build } from '@stencil/core';
import { common } from '../../global/data/common';
import { commonDynamic } from '../../global/data/commonDynamic';
import { ProductCardData } from '../../global/data/listing';
import Cookies from 'js-cookie';

@Component({
  tag: 'ks-compare-popup',
  styleUrl: 'compare-popup.css'
})
export class ComparePopup{
    @Element() root: HTMLElement;

    @Prop({reflect: true, mutable: true}) hide: boolean = false;
    @Prop({reflect: true, mutable: true}) show: boolean = false;
    @Prop({reflect: true, mutable: true}) extend: boolean = true;
    @Prop({reflect: true, mutable: true}) blink: boolean = false;

    extendpanel() {
        this.extend = !this.extend;
    }

    updateCookie() {
        const compare = commonDynamic.get('compare');
        Cookies.set('product-comparison', JSON.stringify(compare), { expires: 60 });
    }

    loadCookie() {
        const data = JSON.parse(Cookies.get('product-comparison'));
        if(data) commonDynamic.set("compare", data);
        return data?.length > 0;
    }

    componentWillLoad() {
        if(this.loadCookie())
            this.showpanel();
    }

    @Method()
    async addProduct(product: ProductCardData) {
        let compare = commonDynamic.get("compare");

        if(!this.show || this.hide) this.showpanel();
        if(!this.extend) this.extendpanel();

        if(compare.length == 3) {
            this.animate_blink();
            return;
        }

        commonDynamic.set("compare", [...compare, {
            id: product.id,
            image: product.image,
            link: product.link,
            name: product.name,
        }]);

        this.updateCookie();
    }

    deleteProduct(id) {
        let compare = commonDynamic.get("compare");
        const index = compare.findIndex(item => item.id == id);

        if(index >= 0) {
            compare.splice(index, 1);

            commonDynamic.set("compare", [...compare]);
            this.updateCookie();

            if(compare.length == 0) this.hidepanel();
        }
    }

    cancel() {
        this.hidepanel();
        commonDynamic.set("compare", []);
        this.updateCookie();
    }

    compare() {
        const compare = commonDynamic.get("compare");
        if(compare.length < 2) return;
        
        window.location.href = "/porownaj-" + compare.map(item => item.id).join("-") + ".html";
    }


    init = false;
    @Method()
    async showpanel() {
        this.show = true;
        this.hide = true;
        setTimeout(() => {
            this.hide = false;
        }, this.init ? common.get('cookieDelay') : 100);
        this.init = true;
    }

    hidepanel() {
        this.hide = true;
    }

    animate_blink() {
        this.blink = true;
        setTimeout(() => {
            this.blink = false;
        }, 500);
    }

    @Listen('scroll', {target: 'window'})
    onScroll() {
        if(window.screen.height < 840)
            this.extend = false;
    }

    render() {
        if (!Build.isBrowser)
            return <Host/>;

        const compare = commonDynamic.get("compare");

        return <Host aria-hidden={this.show ? "false" : "true"}>
            <div class="message">
                <div class="buttons">
                    <ks-button round border narrower icon={"x"} onClick={()=>this.cancel()} />

                    <p>Porównywanie produktów</p>
                    
                    <ks-button round border narrower icon={this.extend ? "chevron-down" : "chevron-up"} onClick={()=>this.extendpanel()} />
                    {this.extend ? 
                        <ks-button round narrower name={"Porównaj"} onClick={()=>this.compare()} disabled={compare?.length < 2}/>
                    : null}
                </div>
            </div>
            <div class="products">
                {[0, 1, 2].map(index => 
                    <div class={"product" + (compare[index] ? "" : " empty")}>
                        {compare[index] ? [
                            <ks-img3 image={compare[index].image} />,
                            <a href={compare[index].link}>{compare[index].name}</a>,
                            <ks-button round border narrower icon={"x"} onClick={()=>this.deleteProduct(compare[index].id)} />
                        ] : null}
                    </div>
                )}
            </div>
        </Host>;
    }
}
