import { Component, h, Prop, Host } from '@stencil/core';
import { ListingInfoData } from '../../global/data/listing';

@Component({
  tag: 'ks-listing-info',
  styleUrl: 'listing-info.css'
})
export class InfoCards {
  @Prop() data: ListingInfoData;

  render() {
    return <Host>
        <ks-img3 image={this.data.image} webp={this.data.webp} width={this.data.width} height={this.data.height} fit='cover' aria-hidden />
        <div class="content">
            <div>
              <h2>{this.data.heading}</h2>
              <p>{this.data.paragraph}</p>
            </div>
            <ks-button narrow round name={this.data.button} link={this.data.link} />
        </div>
    </Host>;
  }
}
