import { Component, h, Prop, Listen, Element } from '@stencil/core';
import { common } from '../../../global/data/common';

@Component({
    tag: 'ks-sorting',
    styleUrl: 'sorting.css'
})
export class Sorting {

    @Element() root: HTMLElement;
    @Prop() post: string;
    @Prop() current: number;

    render() {
        const strings = common.get('translations');

        const menuitem = (index: number, name: string, icon: string) => 
            <button class={this.current == index ? "active" : null}
                onClick={()=> this.sort(index)}
                role="menuitem"
                aria-current={this.current == index ? "true" : null}
                aria-label={`${name}${this.current == index ? " - aktywne" : ""}`}
            >
                {name} <ks-icon name={icon} size={0.9} aria-hidden/>
            </button>

        return(
            <details>
                <form action={this.post} method="post" style={{margin: "0"}} aria-hidden>
                    <input type="hidden" name="sortowanie" value="0"></input>
                </form>

                <summary aria-label={strings.sorting}>
                    {strings.sorting}
                    <ks-icon name="shuffle" size={0.9} aria-hidden/>
                </summary>

                <div class="dropdown" role="menu">
                    {menuitem(1, strings.sortingRecommended, "check")}
                    {menuitem(2, strings.sortingPopular, "users")}
                    {menuitem(3, strings.sortingNewest, "clock")}
                    <hr/>
                    {menuitem(4, strings.sortingCheapest, "plus-circle")}
                    {menuitem(5, strings.sortingExpensive, "minus-circle")}
                </div>
            </details>
        );
    }

    form: HTMLFormElement;
    input: HTMLInputElement;

    componentDidLoad() {
        this.form = this.root.querySelector("form");
        this.input = this.form.querySelector("input");
    }

    sort(value: number){
        this.input.value = value.toString();
        this.form.submit();
    }

    @Listen("click", {target: "window"})
    disable(e: Event){
        if(!this.root.contains(e.target as HTMLElement))
            this.close()
    }

    close() {
        const details = this.root.querySelector('details') as HTMLDetailsElement;
        if (details) details.open = false;
    }
}