ks-sorting {
    display: block;
    position: relative;
}

ks-sorting details {
    position: relative;
}

ks-sorting summary {
    cursor: pointer;
    border: 1px solid #e5e5e5;
    background-color: white;
    padding: 9px 20px;
    list-style: none;
    font-size: 15px;
    text-align: center;
    transition: var(--transition-border-color);
}

ks-sorting summary:hover {
    border-color: #bebebe;
}

ks-sorting summary:active {
    border-color: #8d8d8d;
}

ks-sorting ks-icon {
    margin-left: 5px;
}

ks-sorting .dropdown {
    position: absolute;
    right: 0;
    z-index: 1020;

    box-sizing: border-box;
    min-width: 200px;
    padding: 10px 0;
    margin-top: 8px;

    background: #fff;
    color: #373737;
    box-shadow: 0 5px 20px rgb(0 0 0 / 20%);
}

ks-sorting button {
    display: block;
    position: relative;

    color: var(--color-dark);
    text-decoration: none !important;

    font-size: 14px;
    line-height: 14px;
    padding: 11px 15px;

    border: none;
    width: 100%;
    background: transparent;
    text-align: left;
    cursor: pointer;
}

ks-sorting button.active {
    background-color: #f2f2f2 !important;
}

ks-sorting button:hover { background-color: #fafafa }
ks-sorting button:active { background-color: #f2f2f2 }

ks-sorting button ks-icon {
    position: absolute;
    right: 13px;
    top: 8px;
}

ks-sorting hr {
    overflow: visible;
    margin: 10px 0;
    border: 0;
    border-top: 1px solid #e5e5e5;
}